.offers{
    width: 60%;
    height: 65vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-top: 170px;
    margin-bottom: 150px;
    background: linear-gradient(180deg , #fde1ff 0%, #e1ffea22 60%);
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.offers-left h1{
    color: #171717;
    font-size: 48px;
    font-weight: 500;
}
.offers-left p{
    color: #171717;
    font-size: 20px;
    font-weight: 500;
}
.offers-left button{
width:210px;
height: 45px;
border-radius: 30px;
background: #ff4141;
border: none;
color: white;
font-size: 20px;
font-weight: 500;
margin-top: 30px;
cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}
.offers-right img{
    height: 350px;
}