.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo p{
    color: #171717;
    font-size: 30px;
    font-weight: 500;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 40px;
    color: #626262;
    font-size: 15px;
    font-weight: 400;
}
.nav-menu li{
    display: flex;
    flex-direction: column ;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 90%;
    height: 2px;
    border-radius: 10px;
    background: #ff4141;

}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 40px;
}
.nav-login-cart button{
    width: 110px;
    height: 39px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 70px;
    color: #515151;
    font-size: 17px;
    font-weight: 400;
    background: white;
    cursor: pointer;
}
.nav-login-cart button:active{
    background: #f3f3f3;
}
.nav-cart-count{
    width: 16px;
    height: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -30px;
    margin-left: -50px;
    border-radius: 10px;
    font-size: 10px;
    background: red;
    color: white;
    
}
